  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.boton-ver:hover {
  background-color: #d4e8f4;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
.boton-ver:active {
  transform: scale(0.97);
}
.boton-ver:focus {
  outline: none;
  border-color: #a3cfe0;
}
.boton-ver svg {
  stroke: currentColor;
  width: 20px;
  height: 20px;
}

