.cont-boton {
  margin-top: 3rem;
  display: flex;
  justify-content: space-evenly;
}

.alerta-error {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
  margin: 1rem 0;
  background-color: red;
}

.cerrar-sesion {
  color: rgba(19, 54, 252, 0.7019607843);
  font-size: 1.4rem;
  margin-top: 0.5rem;
}
.cerrar-sesion:hover {
  font-style: italic;
  color: rgba(0, 0, 0, 0.8);
}

.fecha {
  position: absolute;
  top: 0;
  right: 0;
  text-align: end;
}
.fecha p {
  font-size: 1.5rem;
  color: #ffffff;
  font-weight: 700;
  margin-right: 2rem;
}

.header img {
  position: relative;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}

.eliminar {
  margin: 2rem auto 0 auto;
}
.eliminar p {
  color: red;
  font-weight: 700;
}

.tablas {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tablas table {
  max-width: 100%;
  justify-self: center;
  border-spacing: 1;
}
.tablas table th,
.tablas table td {
  text-align: center;
  padding: 0.2rem;
}
.tablas table .vb-cell {
  cursor: pointer;
  position: relative;
}
.tablas .table-left {
  justify-self: start;
}
.tablas p {
  margin: 0;
  text-align: start;
  font-size: 1.3rem;
}
.tablas h3,
.tablas h2,
.tablas h1 {
  justify-self: center;
  margin: 1rem;
}
.tablas h3 {
  font-size: 1.4rem;
  text-transform: uppercase;
}

.diseño_tablas tr:nth-child(odd) {
  background-color: #faf5f5;
}
.diseño_tablas tr:nth-child(even) {
  background-color: #f5e9e8;
}

.menu-barra {
  display: flex;
  justify-content: center;
  background-color: #f5e9e8; /* base muy clara */
  padding: 12px;
  border-radius: 12px;
  margin: 20px auto;
  max-width: 800px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  gap: 8px;
}

.menu-opcion {
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s, transform 0.2s;
  color: #fff;
}

.boton1_nav {
  background-color: #b85c5c; /* lila pastel */
}

.boton2_nav {
  background-color: #8c7c7a; /* verde menta pastel */
}

.menu-opcion:hover {
  transform: translateY(-2px);
  filter: brightness(1.08);
}

@media (max-width: 600px) {
  .menu-barra {
    flex-direction: column;
    align-items: center;
  }
  .menu-opcion {
    width: 100%;
    text-align: center;
  }
}
.custom-file-input {
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding: 10px 20px;
  background-color: #f5e9e8;
  color: #555;
  border: 2px solid #e0cfcf;
  border-radius: 8px;
  font-size: 14px;
  transition: background-color 0.3s;
  overflow: hidden;
}
.custom-file-input:hover {
  background-color: #f2dcdc;
}
.custom-file-input input[type=file] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
.custom-file-input::after {
  content: attr(data-label);
  display: block;
  margin-top: 6px;
  font-size: 12px;
  color: #888;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.instruccion {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.8rem 1rem;
  margin: 1rem 0;
  border-radius: 0.75rem;
  font-size: 0.95rem;
  font-weight: 500;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.instruccion .icon {
  font-size: 1.4rem;
  flex-shrink: 0;
}

.instruccion.info {
  background-color: #e7f2fb; /* azul pastel */
  color: #245d82;
  border: 1px solid #c4e0f3;
}

.instruccion.play {
  background-color: #eaf9ea; /* verde pastel */
  color: #2e7d32;
  border: 1px solid #c6eac7;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: sans-serif;
  font-weight: bold;
}
.toggle-wrapper .label-no {
  color: #d9534f;
}
.toggle-wrapper .label-si {
  color: #5cb85c;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f5e9e8;
  transition: 0.4s;
  border-radius: 30px;
}
.switch .slider::before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.switch input:checked + .slider {
  background-color: #b8e0d2;
}
.switch input:checked + .slider::before {
  transform: translateX(30px);
}

