  width: 100%;
  border-radius: 10px;
}
.visor .barra {
  margin-top: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  /* Barra de progreso personalizada */
}
.visor .barra input[type=range] {
  flex: 1; /* ocupa todo el ancho disponible */
  -webkit-appearance: none; /* quitar estilo predeterminado */
  width: 100%;
  height: 8px;
  border-radius: 5px;
  background: #4CAF50;
  cursor: pointer;
  /* Track de Firefox */
}
