  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #4CAF50;
  cursor: pointer;
  margin-top: -4px; /* centrar el thumb verticalmente */
}
.visor .barra input[type=range]::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #4CAF50;
  cursor: pointer;
}
.visor .barra input[type=range]::-ms-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid #4CAF50;
  cursor: pointer;
}
.visor .barra input[type=range]::-moz-range-track {
  background: #4CAF50;
  height: 8px;
  border-radius: 5px;
}
.visor .barra .boton-play,
.visor .barra .boton-full {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
}
.visor .barra .boton-play:hover,
.visor .barra .boton-full:hover {
  background: #45a049;
}
.visor #completado {
  margin-top: 15px;
  padding: 10px;
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  color: #155724;
  border-radius: 5px;
  display: none;
  font-weight: bold;
  text-align: center;
}

