.cont-boton {
    margin-top: 3rem;
    display: flex;
    justify-content: space-evenly;
}

.alerta-error {
    padding: .5rem;
    text-align: center;
    color: v.$blanco;
    font-weight: v.$regular;
    text-transform: uppercase;
    margin: 1rem 0;
    background-color: v.$rojo;
}

.cerrar-sesion {
    color: v.$azul;
    font-size: 1.4rem;
    margin-top: 0.5rem;
    &:hover {
        font-style: italic;
        color: v.$negroTransparente;
    }
}

.fecha {
    position: absolute;
    top: 0;
    right: 0;
    text-align: end;

    p {
        font-size: 1.5rem;
        color: v.$blanco;
        font-weight: v.$bold;
        margin-right: 2rem;
    }
}

.header {
    img {
        position: relative;
        width: 100%;
        object-fit: cover;
        object-position: center center;
    }
}

.eliminar {
    margin: 2rem auto 0 auto;

    p {
        color: v.$rojo;
        font-weight: v.$bold;
    }
}

.tablas {
    display: flex;
    flex-direction: column;
    align-items: center;

    table {
        max-width: 100%;
        justify-self: center;
        border-spacing: 1;

        th,
        td {
            text-align: center;
            padding: 0.2rem;
        }

        .vb-cell {
            cursor: pointer;
            position: relative;
        }
    }

    .table-left {
        justify-self: start;
    }

    p {
        margin: 0;
        text-align: start;
        font-size: 1.3rem;
    }

    h3,
    h2,
    h1 {
        justify-self: center;
        margin: 1rem;
    }

    h3 {
        font-size: 1.4rem;
        text-transform: uppercase;
    }
}

.diseño_tablas {
    tr:nth-child(odd) {
        background-color: v.$blanco_humo;

    }

    tr:nth-child(even) {
        background-color: v.$cuarto_color;

    }
}

.menu-barra {
  display: flex;
  justify-content: center;
  background-color: v.$cuarto_color; /* base muy clara */
  padding: 12px;
  border-radius: 12px;
  margin: 20px auto;
  max-width: 800px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  gap: 8px;
}

.menu-opcion {
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s, transform 0.2s;
  color: #fff;
}

.boton1_nav {
  background-color: #b85c5c; /* lila pastel */
}

.boton2_nav {
  background-color: #8c7c7a; /* verde menta pastel */
}


.menu-opcion:hover {
  transform: translateY(-2px);
  filter: brightness(1.08);
}

@media (max-width: 600px) {
  .menu-barra {
    flex-direction: column;
    align-items: center;
  }
  
  .menu-opcion {
    width: 100%;
    text-align: center;
  }
}

.custom-file-input {
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding: 10px 20px;
  background-color: #f5e9e8; // color pastel
  color: #555;
  border: 2px solid #e0cfcf;
  border-radius: 8px;
  font-size: 14px;
  transition: background-color 0.3s;
  overflow: hidden;

  &:hover {
    background-color: #f2dcdc;
  }

  input[type="file"] {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
  }

  &::after {
    content: attr(data-label);
    display: block;
    margin-top: 6px;
    font-size: 12px;
    color: #888;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
  }
}

.instruccion {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.8rem 1rem;
  margin: 1rem 0;
  border-radius: 0.75rem;
  font-size: 0.95rem;
  font-weight: 500;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.instruccion .icon {
  font-size: 1.4rem;
  flex-shrink: 0;
}

.instruccion.info {
  background-color: #e7f2fb; /* azul pastel */
  color: #245d82;
  border: 1px solid #c4e0f3;
}

.instruccion.play {
  background-color: #eaf9ea; /* verde pastel */
  color: #2e7d32;
  border: 1px solid #c6eac7;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: sans-serif;
  font-weight: bold;

  .label-no {
    color: #d9534f; // rojo pastel
  }

  .label-si {
    color: #5cb85c; // verde pastel
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;

  input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f5e9e8; // pastel por defecto
    transition: 0.4s;
    border-radius: 30px;

    &::before {
      position: absolute;
      content: "";
      height: 22px;
      width: 22px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      transition: 0.4s;
      border-radius: 50%;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }
  }

  input:checked + .slider {
    background-color: #b8e0d2; // verde pastel cuando está en SÍ
  }

  input:checked + .slider::before {
    transform: translateX(30px);
  }
}




